<template>
  <div class="one">
    <div class="item"><Echarts ref="barRef" :op="opGene()"></Echarts></div>
    <div class="item">
      <Echarts ref="lineRef" :op="opGene('line')"></Echarts>
    </div>
  </div>
</template>

<script>
import Echarts from "@/common/echarts.vue";
export default {
  components: {
    Echarts
  },
  methods: {
    opGene(type = "bar") {
      return {
        title: {
          text: `ECharts ${type} 入门示例`
        },
        tooltip: {},
        xAxis: {
          data: ["衬衫", "羊毛衫", "雪纺衫", "裤子", "高跟鞋", "袜子"]
        },
        yAxis: {},
        series: [
          {
            name: "销量",
            type: type,
            data: type === "line" ? this.barSeriesData : this.lineSeriesData
          }
        ]
      };
    }
  },
  data() {
    return {
      barSeriesData: [5, 20, 36, 10, 10, 20],
      lineSeriesData: [5, 20, 36, 10, 10, 46]
    };
  },
  mounted() {
    this.$refs["barRef"].echartInstance.on("click", params => {
      this.$router.push({ name: "default", params: params });
    });
    this.$refs["lineRef"].echartInstance.on("click", params => {
      this.$router.push({ name: "default", params: params });
    });
    setTimeout(() => {
      this.barSeriesData = [5, 20, 36, 10, 10, 98];
      this.lineSeriesData = [5, 20, 36, 10, 10, 86];
      this.opGene();
    }, 2000);
  }
};
</script>
