<template>
  <div class="echart-wrapper" :id="echartId"></div>
</template>

<script>
import echarts from "echarts";
import * as helper from "@/util/helper.js";

export default {
  props: {
    op: {
      require: true,
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      echartInstance: null,
      echartId: this.geneId()
    };
  },
  watch: {
    op(oldVal, newVal) {
      this.init();
    }
  },
  methods: {
    init() {
      this.echartInstance = echarts.init(
        document.getElementById(this.echartId)
      );
      this.echartInstance.setOption(this.op);
      // window.addEventListener('resize', () => {
      //   this.echartInstance.resize()
      // })
      helper.addEventHandler("resize", e => {
        this.echartInstance.resize();
        // console.log('e', e)
      });
    },
    /**
     * 生成id
     */
    geneId() {
      return Date.now() + "-" + Math.random();
    }
  },
  mounted() {
    this.init();
  }
};
</script>
<style lang="less" scoped>
.echart-wrapper {
  height: 100%;
  width: 100%;
  background-color: #fff;
}
</style>
